

body {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.container {
    width: 500px;
    height: 500px;
    background: gray;
    display: flex;
    flex-direction: column;
}

.row {
    flex: 1;
    display: flex;
    width: 100%;
}

.item {
    flex: 1;
}